import { createRouter, createWebHistory } from "vue-router";

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes: [
    {
      path: "/",
      name: "root",
      redirect: "/home",
    },
    {
      path: "/home",
      name: "home",
      component: () => import("@/views/HomeView.vue"),
    },
    {
      path: "/login",
      name: "login",
      component: () => import("@/views/LoginView.vue"),
    },
    {
      path: "/hospitalzone",
      name: "HospitalZone",
      component: () => import("@/views/HospitalZoneView.vue"),
    },
    {
      path: "/deptlist",
      name: "DeptList",
      component: () => import("@/views/DeptListView.vue"),
    },
    {
      path: "/deptschfordoc",
      name: "DeptSchForDoc",
      component: () => import("@/views/DeptSchForDocView.vue"),
    },
    {
      path: "/doctordetails",
      name: "DoctorDetails",
      component: () => import("@/views/DoctorDetailsView.vue"),
    },
  ],
});

export default router;
